  .rating {
    display: flex;
    align-items: center;
    >span {
      display: inline-flex !important;
      align-items: center;
      gap: 0px;
    }
    .br-selected {
      display: block;
      width: 12px;
      padding: 5px 0;
      height: 28px;
      float: left;
      background-color: #90e1e1;
      margin: 1px;
      text-align: center;
    }
    .br-widget {
      display: block;
      width: calc(40px + (60 - 40) * ((100vw - 320px) / (1920 - 320)));
      height: 8px;
      float: left;
      background-color: #90e1e1;
      margin: 1px;
    }
    .br-current {
      background-color: $primary-color !important;
      color: $white !important;
    }
    .square-number {
      display: block;
      width: 30px;
      height: 30px;
      float: left;
      border: 2px solid #90e1e1;
      background-color: rgb(255 255 255);
      margin: 2px;
      text-decoration: none;
      font-size: 14px;
      line-height: 1.8;
      text-align: center;
      color: rgb(143 162 227);
      font-weight: 600;
      &.active {
        border: 2px solid $primary-color;
        color: $primary-color;
      }
    }
    &.reverse {
      .square-number {
        width: 22px;
        height: 22px;
      }
    }
    .text-primary {
      color: $primary-color !important;
    }
    .text-body {
      color: $theme-body-font-color;
    }
    .star {
      color: #ffa800;
    }
    &.pill-rating-list {
      .pill-rating {
        padding: 7px 15px;
        background-color: #90e1e1;
        color: var(--theme-default);
        text-decoration: none;
        font-size: 13px;
        text-align: center;
        font-weight: 400;
      }
      >span {
        >span {
          &:first-of-type {
            .pill-rating {
              border-top-left-radius: 999px;
              border-bottom-left-radius: 999px;
            }
          }
          &:last-of-type {
            .pill-rating {
              border-top-right-radius: 999px;
              border-bottom-right-radius: 999px;
            }
          }
        }
      }
    }
    .current-rating {
      font-size: 20px;
      line-height: 1;
      color: var(--theme-default);
      font-weight: 400;
    }
  }
  .reactour__popover {
    background-color: $primary-color !important;
    color: $white !important;
    span {
      background-color: $primary-color !important;
      padding: 0 10px !important;
      border-radius: 100% !important;
      border: 1px solid $white;
      height: unset !important;
    }
    .reactour__close-button {
      top: -18px !important;
      right: -18px !important;
      color: $white;
      height: 12px;
      width: 12px;
    }
    >div[dir] {
      >button {
        display: flex !important;
        color: $white !important;
        svg {
          color: $white !important;
        }
      }
    }
  }
  .text-base {
    font-size: 16px;
  }
